.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: rx.jpeg infinite 20s linear;
          animation: rx.jpeg infinite 20s linear;
  background: url('rx.jpeg');
  height: 80px;
}

.Navpills {
  background-color: blue;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-header {
  background-color: blue;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes rx.jpeg {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes rx.jpeg {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.nav nav-tabs {
  font-size: 140px;
}

/* unvisited link */
a:link {
    color: purple;
}

/* visited link */
a:visited {
    color: #990000;
}

/* mouse over link */
a:hover {
    color: black;
}

/* selected link */
a:active {
    color: blue;
}




